<template>
  <header class="header">
    <h1>TODOS</h1>
    <input
        class="new-todo"
        placeholder="What needs to be done?"
        v-model="text"
        @keydown.enter="addTodo"
        autofocus>
  </header>
</template>

<script>
  export default {
    data() {
      return {
        text: '',
      }
    },
    methods: {
      addTodo() {
        const text = this.text.trim()
        if (text === '') {
          return
        }

        this.$store.commit('addTodo', text)

        this.text = ''
      }
    }
  }
</script>
