<template>
  <li :class="{ completed: todo.isDone, editing: isEdit }" @dblclick="editTodo">
    <div class="view">
      <input
          class="toggle"
          type="checkbox"
          v-model="todo.isDone"
      />
      <label>{{ todo.text }}</label>
      <button class="destroy" @click="removeTodo"></button>
    </div>
    <input
        ref="edit"
        class="edit"
        v-model="text"
        @blur="changeTodo"
        @keypress.enter="changeTodo"
    >
  </li>
</template>

<script>
  export default {
    props: {
      todo: {
        type: Object,
        required: true,
        default() {
          return {
            id: -1,
            text: '',
            isDone: false,
          }
        },
      },
    },
    data() {
      return {
        isEdit: false,
        text: this.todo.text,
      }
    },
    methods: {
      removeTodo() {
        this.$store.commit('removeTodo', this.todo.id)
      },
      editTodo() {
        if (this.isEdit) {
          return
        }

        this.isEdit = true

        this.text = this.todo.text

        this.$nextTick(() => {
          this.$refs.edit.focus()
        })
      },
      changeTodo() {
        const id = this.todo.id
        const text = this.text

        this.$store.commit('changeTodo', { id, text })

        this.isEdit = false
      }
    }
  }
</script>
