<template>
  <footer class="footer">
    <span class="todo-count"><strong>{{ remain }}</strong> item left</span>
    <ul class="filters">
      <todo-filter
        v-for="filter in filters"
        :key="filter"
        :current="current"
        :status="filter" />
    </ul>
    <button
        class="clear-completed"
        v-show="showClear"
        @click="clearCompleted"
    >
      Clear completed
    </button>
  </footer>
</template>

<script>
  import TodoFilter from './components/TodoFilter'

  export default {
    components: {
      TodoFilter,
    },
    data() {
      return {
        filters: [ 'all', 'active', 'completed' ],
      }
    },
    computed: {
      remain() {
        return this.$store.getters.remain
      },
      showClear() {
        return this.$store.getters.count !== this.remain
      },
      current() {
        return this.$store.state.status
      }
    },
    methods: {
      clearCompleted() {
        this.$store.commit('clearCompleted')
      }
    }
  }
</script>
