<template>
  <li>
    <router-link :class="{ selected: isSelected }" :to="`/${status}`">{{ status | toUpper }}</router-link>
  </li>
</template>

<script>
  export default {
    props: {
      status: {
        type: String,
        required: true,
        default: 'all',
      },
    },
    computed: {
      isSelected() {
        return this.status === this.$store.state.status
      }
    },
    filters: {
      toUpper(val) {
        const cap = val[0].toUpperCase()
        const rest = val.substr(1)
        return `${cap}${rest}`
      }
    }
  }
</script>
