<template>
  <section class="todoapp">
    <todo-header />
    <todo-main />
    <todo-footer />
  </section>
</template>

<script>
  import TodoHeader from './header/TodoHeader'
  import TodoMain from './main/TodoMain.vue'
  import TodoFooter from './footer/TodoFooter'

  export default {
    components: {
      TodoHeader, TodoMain, TodoFooter,
    },
    beforeRouteUpdate(to, from, next) {
      this.$store.commit('changeStatus', to.params.status)
      next()
    },
    mounted() {
      this.$store.commit('changeStatus', this.$route.params.status)
    }
  }
</script>
