import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let uniqId = 0

export default new Vuex.Store({
  state: {
    status: 'all',
    _todos: [],
  },
  mutations: {
    addTodo(state, text) {
      const todo = {
        id: uniqId++,
        text,
        isDone: false,
      }

      state._todos.push(todo)
    },
    removeTodo(state, id) {
      state._todos = state._todos.filter(todo => todo.id !== id)
    },
    clearCompleted(state) {
      state._todos = state._todos.filter(todo => !todo.isDone)
    },
    setDone(state, isDone) {
      state._todos = state._todos.map(todo => {
        return Object.assign({}, todo, { isDone })
      })
    },
    changeStatus(state, status) {
      state.status = status || 'all'
    },
    changeTodo(state, payload) {
      state._todos = state._todos.map(todo => {
        if (todo.id !== payload.id) {
          return todo
        }

        return Object.assign({}, todo, { text: payload.text })
      })
    }
  },
  getters: {
    todos(state) {
      switch (state.status) {
        case 'active':
          return state._todos.filter(todo => !todo.isDone)

        case 'completed':
          return state._todos.filter(todo => todo.isDone)

        default:
          return state._todos
      }
    },
    count(state) {
      return state._todos.length
    },
    remain(state) {
      return state._todos.filter(todo => !todo.isDone).length
    }
  },
})
