<template>
  <ul class="todo-list">
    <todo-item
        v-for="todo in todos"
        :key="todo.id"
        :todo="todo"
    />
  </ul>
</template>

<script>
  import TodoItem from './TodoItem'

  export default {
    components: {
      TodoItem,
    },
    computed: {
      todos() {
        return this.$store.getters.todos
      }
    }
  }
</script>
