<template>
  <section class="main">
    <input id="toggle-all" class="toggle-all" type="checkbox" v-model="toggleAll">
    <label for="toggle-all">Mark all as complete</label>
    <todo-list />
  </section>
</template>

<script>
  import TodoList from './components/TodoList'

  export default {
    components: {
      TodoList,
    },
    computed: {
      toggleAll: {
        get() {
          return this.$store.getters.todos.every(todo => todo.isDone)
        },
        set(val) {
          this.$store.commit('setDone', val)
        }
      }
    }
  }
</script>
